import React from 'react';

const Card = (props) => {
  
  let {name, species, gender, origin, image} = props.info;
  
  return (
    <div className="card">
    <img src={image} alt='Snow Jhon'/>
    <div className="card-text">
      <h3>{name}</h3>
      <div>
        <p>{species}</p>
        <p>{gender}</p>
        <p>Origin: {origin.name}</p>
      </div>
    </div>
    </div>
    )
  }
  
  export default Card;