import React from 'react';
import Card from './Card';

const Container = (props) => {
  let { data } = props;
  let charactersCard = data.map(element => {
    return <Card key={element.id} info={element}></Card>
  })
  return (
    <div className="cardContainer">
      {charactersCard}
    </div>
    )
}

export default Container;